@import "./styles/menu.scss";
@import "./styles/circleOfFifths.scss";
@import "./styles/loadingScreen.scss";
@import "./styles/toggleSwitch.scss";
@import "./styles/notes.scss";
@import "./styles/musicalStaff.scss";
@import "./styles/share.scss";

h1,
p {
  font-family: Lato;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #f1f1da;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  font-family: "Quicksand", sans-serif;
}

#root,
.Piano {
  height: 100%;
  min-width: 100%;
}

.Piano {
  display: flex;
  flex: 1;
  /*flex-direction: column;*/
}

/* keyboard */

.Keyboard {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: center; /* align items in Main Axis */
  align-items: stretch;
}

/** Key **/

.Key {
  flex: 1;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  pointer-events: none; /* makes it click thru */
  position: relative;
}

.Key .note {
  opacity: 0.4;
  text-align: center;
  font-weight: 700;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
}

.Key.on.active .color-key {
  box-shadow: inset 0px 0px 3px black;
}

.c-mark {
  border-left: 3px solid #000000;
}

.Key .seventh {
  position: absolute;
  top: 15%;
  font-size: 24px;
  text-align: center;
}

