.Key .piano-key {
  border-top: 0px;
  z-index: 2;
  border-bottom: 1px solid #3f3f3f;
}

.Key.black {
  z-index: 3;
}

.Key.black .piano-key {
  height: 30%;
  background-color: transparent;
}

.Key.black .blackPianoKeyContainer {
  height: 100%;
}
.Key.black .piano-key .blackPianoKeyFiller {
  height: 35%;
}
.Key.black .piano-key .blackPianoKey {
  pointer-events: all;
  height: 65%;
  background-color: black;
}

.Key.white .piano-key {
  height: 30%;
  background-color: white;
  border-left: 1px solid #3f3f3f;
  width: 171%;
  position: relative;
  pointer-events: all;
}

.Key.on .piano-key.white:hover,
.Key.on .piano-key.black .blackPianoKey:hover {
  background-color: #aaa;
}

.Key.off .piano-key.white {
  background-color: #ddd;
}
.Key.off .piano-key.black .blackPianoKey {
  background-color: #666;
}

.Key.on .color-key {
  pointer-events: all;
}

.piano-key.c {
  left: 0%;
}
.piano-key.d {
  left: -34%;
}
.piano-key.e {
  left: -65%;
}
.piano-key.f {
  left: 6%;
}
.piano-key.g {
  left: -22%;
}
.piano-key.a {
  left: -50%;
}
.piano-key.b {
  left: -76%;
}

.piano-key.e,
.piano-key.b {
  border-right: 1px solid #3f3f3f;
}

.black {
  border-top: 0px;
  z-index: 2;
}

.text {
  text-align: center;
}
